.home-container {
  margin: 0px;
  min-width: 100%;

}

.welcome-banner {
  min-width: 100%;
height:40%;
background: url('../Assets/Group\ 21.png') center center no-repeat;
background-size: cover;
box-shadow: 0 6px 8px 0 rgba(0, 0, 0, 0.3);
}

.flex-container {
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
  align-items: left;
  width: 100%;
  height: fit-content;
}

.sasc-title {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 40%;

}

.welcome-section {
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.welcome-section p {
  margin: 0 10%;
  font-size: 20px;
  font-weight: 400;
  line-height: 1.5;
}

.photo-container {
  width: 100%;
}

.photo-carousel {
  margin: 0 auto;
}

.logo-row {
  display: flex;
  flex-direction: row;
  justify-content: space-around;
  align-items: center;
  width: 100%;
  height: 100%;
  margin: 20px 10px;
}

.aquamark-img {
  border-radius: 0;
}

@media (max-width: 1400px) {
  .sasc-title img {
    width: 300px;
  }

  .welcome-section h1 {
    font-size: 1.5em;
  }

  .welcome-section p {
    font-size: 1.2em;
  }

}
@media (max-width: 1200px) {
  .sasc-title {
display: none;  }

  .welcome-section h1 {
    font-size: 1.2em;
  }

}
@media (max-width: 900px) {
  .home-container {
    overflow-y: scroll;
    overflow-x: hidden;
  }
}

@media (max-width: 700px) {
  

  .flex-container {
    flex-direction: column;
    justify-content: center;
    align-items: center;
  }

  .sasc-title {
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    
  }
  .sasc-title img {
    margin: 20px 0;
    width: 200px;
  }
  .aquamark-img {
    width: 80px;
    padding-right: 20px;
    border-radius: 0;
  }
  .welcome-title {
    text-align: center;
  }
}



